/* Landing Page */
.landing {
    
    position: relative;
    background: url('./Assets/img/background.jpg') no-repeat center center/cover;
    height: 100vh;
  }
  
  .landing-inner {
    color: #fff;
    height: 100%;
    width: 80%;
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
  }
  /* Text Styles */
  .x-large {
    font-size: 3rem;
    color: black;
  }

  .large {
    font-size: 2rem;
  }

  .lead {
    font-size: 1.5rem;
    color: rgb(3, 43, 44);
  }

  .p {
    padding: 0.5rem;
  }
  /* Overlay */
.dark-overlay {
  background-color: rgba(116, 104, 93, 0.7);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}